@tailwind base;
@tailwind components;
@tailwind utilities;
.button-primary-extend, .search-form .search-submit, .woocommerce-form-coupon .button, .woocommerce-account .woocommerce-form-login__submit,
.woocommerce-account .woocommerce-form-register__submit, .woocommerce div.product .woocommerce-Tabs-panel .form-submit input[type=submit], .woocommerce .woocommerce-message .button.wc-forward, .woocommerce div.product .single-product-summary form.cart .button {
  @apply flex items-center justify-center px-6 py-2 rounded-md font-medium transition-colors duration-200 text-base font-normal h-[3rem] !bg-[linear-gradient(45deg,rgb(239,35,60)_0%,rgb(244,113,3)_100%)] !text-white hover:!bg-[linear-gradient(45deg,rgb(239,35,60)_0%,rgb(244,113,3)_100%)];
}

@layer components {
  .btn {
    @apply inline-flex items-center justify-center px-4 py-2 rounded-md font-medium transition-colors duration-200;
  }
  .btn-primary {
    @apply bg-[linear-gradient(45deg,rgb(239,35,60)_0%,rgb(244,113,3)_100%)] text-white hover:bg-[linear-gradient(45deg,rgb(239,35,60)_0%,rgb(244,113,3)_100%)];
  }
  .btn-secondary {
    @apply bg-secondary text-white hover:bg-secondary/90;
  }
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
/* Swiper */
.swiper-pagination--default {
  --swiper-pagination-bullet-horizontal-gap: 3px;
  @apply inline-flex items-center justify-center absolute h-[28px] bg-white rounded-full py-0 px-2 !bottom-4 !end-4 !w-auto !left-auto;
}

.swiper-pagination--default .swiper-pagination-bullet {
  @apply rounded-full transition-all duration-300;
}

.swiper-pagination--default .swiper-pagination-bullet-active {
  @apply w-4 bg-primary;
}

.woocommerce form .form-row .input-text,
.woocommerce form .form-row .input-email,
.woocommerce form .form-row .input-tel,
.woocommerce form .form-row .input-number,
.woocommerce form .form-row .input-url,
.woocommerce form .form-row .input-search,
.woocommerce form .form-row .input-password {
  @apply border border-gray-300 rounded px-4 py-2 text-base font-normal w-full mb-4 bg-white text-gray-900 focus:border-primary focus:ring-primary focus:ring-1 focus:ring-offset-0 focus:outline-none h-14;
}
.woocommerce form .form-row .select2-container .select2-dropdown, .woocommerce form .form-row .select2-container .select2-selection,
.woocommerce form .form-row .select2-container--default .select2-selection--single {
  @apply border border-gray-300 rounded text-base font-normal w-full;
}
.woocommerce form .form-row .select2-container--default .select2-selection--single .select2-selection__rendered {
  @apply px-4 pb-2 pt-4 text-base font-normal h-14;
}

.woocommerce div.product .single-product-summary .product_title {
  @apply text-xl xl:text-2xl 2xl:text-3xl font-bold mb-4 text-gray-800;
}
.woocommerce div.product .single-product-summary .woocommerce-product-rating {
  @apply mb-0;
}
.woocommerce div.product .single-product-summary .woocommerce-product-rating .star-rating {
  @apply mt-[.8em] text-yellow-500 text-[0.8em];
}
.woocommerce div.product .single-product-summary .price del {
  @apply text-[80%] text-gray-700;
}
.woocommerce div.product .single-product-summary .price ins {
  @apply font-semibold text-primary text-[1.875rem] no-underline;
}
.woocommerce div.product .single-product-summary .woocommerce-product-details__short-description {
  @apply mt-2 mb-5 text-gray-700;
}
.woocommerce div.product .single-product-summary .quantity-wrapper button.minus,
.woocommerce div.product .single-product-summary .quantity-wrapper button.plus {
  @apply h-[3rem];
}
.woocommerce div.product .single-product-summary form.cart {
  @apply before:!content-none after:!content-none;
}
.woocommerce div.product .single-product-summary .woocommerce-product-details__short-description + .stock {
  @apply hidden;
}
.woocommerce div.product .single-product-summary form.cart div.quantity {
  @apply mr-4;
}

.woocommerce .woocommerce-message {
  @apply border-t-primary border-t-4 bg-[#e6fcf5] !text-[#099268];
}
.woocommerce div.product .flex-control-thumbs {
  @apply grid grid-cols-[repeat(auto-fit,minmax(80px,1fr));] gap-4 !mt-6;
}
.woocommerce div.product .flex-control-thumbs li {
  @apply min-w-full w-full aspect-square border border-[#dee2e6] rounded-md overflow-hidden;
}
.woocommerce div.product .flex-control-thumbs li img {
  @apply w-full h-full object-cover;
}
.woocommerce div.product .variations select {
  @apply border border-gray-300 rounded px-4 py-2 text-base font-normal w-full mb-4 bg-white text-gray-900 focus:border-primary focus:ring-primary focus:ring-1 focus:ring-offset-0 focus:outline-none h-14;
}
.woocommerce div.product .single_variation_wrap .woocommerce-variation-availability {
  @apply mb-4;
}
.woocommerce div.product .single_variation_wrap .woocommerce-variation-availability .stock {
  @apply text-primary font-semibold;
}
.woocommerce div.product .woocommerce-Tabs-panel h2 {
  @apply text-xl xl:text-2xl 2xl:text-3xl font-bold mb-3 text-gray-800;
}
.woocommerce div.product .related.products > h2 {
  @apply text-xl xl:text-2xl 2xl:text-3xl font-bold mb-8 text-gray-800;
}
.woocommerce div.product .related.products {
  @apply mt-10;
}
.woocommerce div.product #review_form #respond textarea {
  @apply border border-gray-300 rounded px-4 py-2 text-base font-normal w-full mb-4 bg-white text-gray-900 focus:border-primary focus:ring-primary focus:ring-1 focus:ring-offset-0 focus:outline-none min-h-20;
}
.woocommerce div.product .comment-form-comment label,
.woocommerce div.product .comment-form-rating label,
.woocommerce div.product .comment-form-author label,
.woocommerce div.product .comment-form-email label,
.woocommerce div.product .comment-form-url label {
  @apply block mb-2 text-lg font-medium text-gray-700;
}
.woocommerce div.product .woocommerce-tabs ul.tabs {
  @apply flex flex-wrap gap-x-0 gap-y-2 p-0;
}
.woocommerce div.product .woocommerce-tabs ul.tabs li {
  @apply list-none m-0 rounded-none font-normal border-gray-300 before:!content-none after:!content-none bg-transparent;
}
.woocommerce div.product .woocommerce-tabs ul.tabs li:not(:last-child) {
  @apply border-r-0;
}
.woocommerce div.product .woocommerce-tabs ul.tabs li a {
  @apply text-sm xl:text-base font-normal;
}
.woocommerce div.product .woocommerce-tabs ul.tabs li.active {
  @apply bg-[linear-gradient(45deg,rgb(239,35,60)_0%,rgb(244,113,3)_100%)] text-white border-primary border-b-2;
}
.woocommerce div.product .woocommerce-tabs .panel p,
.woocommerce div.product .woocommerce-tabs .panel ul,
.woocommerce div.product .woocommerce-tabs .panel ol {
  @apply mb-2;
}
.woocommerce div.product .woocommerce-tabs .panel ul {
  @apply list-disc list-inside;
}
.woocommerce div.product .woocommerce-tabs .panel ol {
  @apply list-decimal list-inside;
}

.woocommerce-account form.checkout_coupon,
.woocommerce-account form.login,
.woocommerce-account form.register {
  @apply border-none p-0 m-0 bg-transparent;
}
.woocommerce-account form .password-input input[name=password] {
  margin-bottom: 0 !important;
}
.woocommerce-account form .woocommerce-privacy-policy-text {
  @apply text-sm text-gray-700 mt-4;
}
.woocommerce-account form .woocommerce-privacy-policy-text a {
  @apply text-primary;
}
.woocommerce-account form .woocommerce-privacy-policy-text a:hover {
  @apply text-primary/80;
}
.woocommerce-account form label {
  @apply block mb-1 leading-[1] text-base font-medium text-black;
}

.woocommerce-form-coupon .input-text[name=coupon_code] {
  @apply border border-gray-300 rounded px-4 py-2 text-base font-normal w-full mb-4 bg-white text-gray-900 focus:border-primary focus:ring-primary focus:ring-1 focus:ring-offset-0 focus:outline-none !h-[3rem];
}
.woocommerce-info {
  @apply bg-dark !text-white !border-primary !border-t-4;
}
.woocommerce-info a {
  @apply text-primary;
}

.search-form {
  @apply flex items-center gap-2;
}
.search-form > label {
  @apply flex-1;
}
.search-form input[type=search] {
  @apply border border-gray-300 rounded px-4 py-2 text-base font-normal w-full mb-0 bg-white text-gray-900 focus:border-primary focus:ring-primary focus:ring-1 focus:ring-offset-0 focus:outline-none !h-[3rem];
}